<template>
  <el-row class="main-content">
    <el-col class="left-content" :span="12">
      <el-row justify="center" align="middle">
        <el-col :span="24">
          <el-row justify="center"><div class="main-image"></div></el-row>
          <div class="explanation">       .</div>
          <el-button type="text" @click="clickTTS">
            <span class="iconify" data-inline="false" data-icon="el:speaker" style="font-size: 100px;"></span>
          </el-button>
        </el-col>
      </el-row>
    </el-col>
    <BackButton/>
    <el-col class="right-content" :span="12">
      <el-row justify="center" align="middle">
        <el-col :span="20">
          <el-container class="signup-container">
            <el-header>
              <div class="info-font"></div>
            </el-header>
            <el-main>
              <el-form :model="state.signupForm" :rules="state.rules" ref="signupForm" label-position="top">
                <el-form-item prop="userId" label=" ." :label-width="state.formLabelWidth" >
                  <el-row>
                    <el-col :span="18">
                      <el-input v-model="state.signupForm.userId" autocomplete="off"></el-input>
                    </el-col>
                    <el-col :span="6">
                      <el-button type="info" @click="clickOverlap"></el-button>
                    </el-col>
                  </el-row>
                </el-form-item>
                <el-form-item prop="userPassword" label=" ." :label-width="state.formLabelWidth">
                  <el-input v-model="state.signupForm.userPassword" autocomplete="off" show-password ></el-input>
                </el-form-item>
                <el-form-item prop="userPasswordConfirmation" label="   ." :label-width="state.formLabelWidth">
                  <el-input v-model="state.signupForm.userPasswordConfirmation" autocomplete="off" show-password ></el-input>
                </el-form-item>
                <el-form-item prop="userName" label=" ." :label-width="state.formLabelWidth" >
                  <el-input v-model="state.signupForm.userName" autocomplete="off" ></el-input>
                </el-form-item>
                <el-form-item prop="userGender" label=" .">
                  <el-row justify="start">
                    <el-radio-group v-model="state.signupForm.userGender">
                      <el-radio label="M"></el-radio>
                      <el-radio label="F"></el-radio>
                    </el-radio-group>
                  </el-row>
                </el-form-item>
                <el-row justify="start"> .</el-row>
                <br>
                <el-row>
                  <el-col :span="4">
                    <el-form-item prop="userBirthYear">
                      <el-input v-model="state.signupForm.userBirthYear" placeholder=""></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="1"></el-col>
                  <el-col :span="3" :offset="1">
                    <el-form-item prop="userBirthMonth">
                      <el-input v-model="state.signupForm.userBirthMonth" placeholder=""></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="1"></el-col>
                  <el-col :span="3" :offset="1">
                    <el-form-item prop="userBirthDay">
                      <el-input v-model="state.signupForm.userBirthDay" placeholder=""></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="1"></el-col>
                </el-row>
                <el-form-item prop="userAddress" label=" ." :label-width="state.formLabelWidth">
                  <el-row justify="start">
                    <el-button @click="execDaumPostcode"> </el-button>
                  </el-row>
                  <el-input v-model="state.signupForm.userAddress" placeholder=""></el-input>
                  <el-input id="detailAddress" v-model="state.signupForm.userDetailAddress" placeholder=""></el-input>
                </el-form-item>

                <el-form-item prop="userPhone" label="  ." :label-width="state.formLabelWidth">
                  <el-row>
                    <el-col :span="12">
                      <el-input v-model="state.signupForm.userPhone" placeholder="  ."></el-input>
                    </el-col>
                    <el-col :span="6">
                      <el-button type="info" @click="sendAuthenticationNumber"></el-button>
                    </el-col>
                  </el-row>
                  <el-row style="margin-top: 2px;">
                    <el-col :span="8">
                      <el-input v-model="state.signupForm.authenticationNumber" placeholder=" "></el-input>
                    </el-col>
                    <el-col :span="5">
                      <el-button type="info" @click="confirmAuthenticationNumber"></el-button>
                    </el-col>
                    <el-col :span="5" v-if="state.isAuthNumConfirmed">
                      
                    </el-col>
                  </el-row>
                </el-form-item>
                <el-form-item prop="userEmergency" label="(, ) ." :label-width="state.formLabelWidth">
                  <el-input v-model="state.signupForm.userEmergency" autocomplete="off"></el-input>
                </el-form-item>
              </el-form>
            </el-main>
            <div class="info-font">  ?</div>
            <el-row justify="center">
              <el-button class="select-btn" @click="clickSignup" :disabled="state.isDisabled">
                <span class="iconify" data-inline="false" data-icon="noto:man-gesturing-ok" style="font-size: 100px;"></span>
                <div class="select-font"> ~</div>
              </el-button>
            </el-row>
          </el-container>
        </el-col>
      </el-row>
    </el-col>
  </el-row>
</template>

<script>
// import { useRouter } from 'vue-router'
import { reactive, computed, ref } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import BackButton from '@/views/components/BackButton'
export default {
  name: 'Signup',
  components: {
    BackButton
  },
  // setup() {
  //   const router = useRouter()

  // }
  setup(props) {
    const store = useStore()
    const router = useRouter()
    //      -  . <return>
    const signupForm = ref(null)
    /*
      // Element UI Validator
      // rules    form      
      //
    */

    const validateConfirm = function(rule, value, callback) {
      if (value !== state.signupForm.userPassword) {
        callback(new Error('  .'))
      } else {
        callback()
      }
    }
    const state = reactive({
      signupForm: {
        userId: '',
        userPassword: '',
        userPasswordConfirmation: '',
        userName: '',
        userGender: '',
        userBirth: '',
        userBirthYear: '',
        userBirthMonth: '',
        userBirthDay: '',
        userAddress: '',
        userDetailAddress: '',
        userPhone: '',
        userEmergency: "",
        authenticationNumber: "",
      },
      
      rules: {
        userId: [
          { required: true, message: '  .', trigger: 'blur' },
          { min: 4, message: ' 4  .', trigger: 'blur' },
          { max: 15, message: ' 15  .', trigger: 'blur' }
        ],
        userPassword: [
          { required: true, message: '  .', trigger: 'blur' },
          { min: 8, message: ' 8  .', trigger: 'blur' },
          { max: 16, message: ' 16  .', trigger: 'blur' },
        ],
        userPasswordConfirmation: [
          { validator: validateConfirm, trigger: 'blur' },
          { required: true, message: '  .', trigger: 'blur' },
          { min: 8, message: ' 8  .', trigger: 'blur' },
          { max: 16, message: ' 16  .', trigger: 'blur' },
        ],
        userName: [
          { required: true, message: '  .', trigger: 'blur' },
          { max: 30, message: ' 30  .', trigger: 'blur' }
        ],
        // userGender: [
        //   { required: true, message: '  .', trigger: 'blur' },
        // ],
        userBirthYear: [
          { required: true, message: '  .', trigger: 'blur' },
        ],
        userBirthMonth: [
          { required: true, message: '  .', trigger: 'blur' },
        ],
        userBirthDay: [
          { required: true, message: '  .', trigger: 'blur' },
        ],
        userAddress: [
          { required: true, message: '  .', trigger: 'blur' },
        ],
        userPhone: [
          { required: true, message: '  .', trigger: 'blur' },
        ],
        // emergency: [
        //   { required: false, message: '  .', trigger: 'blur' },
        // ],
      },
      dialogVisible: computed(() => props.open),
      formLabelWidth: '200px',
      isDisabled: true,
      isAuthNumConfirmed: false,
    })

    const clickSignup = function () {
      //    validate      ,  API    
      signupForm.value.validate((valid) => {
        if (valid) {
          store.commit('root/loadingOn')
          state.signupForm.userAddress = state.signupForm.userAddress + ' ' + state.signupForm.userDetailAddress
          state.signupForm.userBirth = state.signupForm.userBirthYear + '-' + state.signupForm.userBirthMonth + '-' + state.signupForm.userBirthDay
          store.dispatch('root/requestSignup', state.signupForm)
          .then(function () {
            store.commit('root/loadingOff')
          })
          .then(() => {
            initSignupForm()
            router.push({
              name: 'Login'
            })
          })
          .catch(function (err) {
            store.commit('root/loadingOff')
            alert("  .", err)
          })
        } else {
          alert('  .')
        }
      });
    }

    const clickOverlap = function() {
      const userId = state.signupForm.userId
      store.commit('root/loadingOn')
      store.dispatch('root/requestOverlapped', {userId: userId})
      .then((res) => {
        store.commit('root/loadingOff')
        if (res.data.data) {
          alert("  .")
        } else {
          alert(" .")
        }
      })
      .catch((err) => {
        store.commit('root/loadingOff')
        alert(err)
      })
    }

    const initSignupForm = function () {
      state.signupForm.userId = ''
      state.signupForm.userPassword = ''
      state.signupForm.userPasswordConfirmation = ''
      state.signupForm.userName = ''
      state.signupForm.userGender = ''
      state.signupForm.userBirth = ''
      state.signupForm.userBirthYear = ''
      state.signupForm.userBirthMonth = ''
      state.signupForm.userBirthDay = ''
      state.signupForm.userAddress = ''
      state.signupForm.userDetailAddress = ''
      state.signupForm.userPhone = ''
      state.signupForm.userEmergency = ''
    }

    const checkValidation = function () {
      // signupForm.value.validate((valid) => {
      //   if (valid) {
      //     state.isDisabled = false
      //   } else {
      //     state.isDisabled = true
      //   }
      // })
      // state.isDisabled = true
    }

    const execDaumPostcode = function() {
      new window.daum.Postcode({
        oncomplete: function(data) {
          //        .

          var addr = ''; //  
          // var extraAddr = ''; //  

          //        .
          if (data.userSelectedType === 'R') { //     
              addr = data.roadAddress;
          } else { //     (J)
              addr = data.jibunAddress;
          }

          state.signupForm.userAddress = addr;
          //    .
          document.getElementById("detailAddress").focus();
        }
      }).open();
    }


    //   
    const sendAuthenticationNumber = function() {
      const payload = {
        authValid: true,
        phoneNumber: state.signupForm.userPhone
      }
      store.commit('root/loadingOn')
      store.dispatch('root/requestAuthenticationNumber', payload)
      .then(() => {
        store.commit('root/loadingOff')
        alert(' .')
      }) 
      .catch(() => {
        store.commit('root/loadingOff')
        alert('  .')
      })
    }

    const confirmAuthenticationNumber = function() {
      const param = {
        phoneNumber: state.signupForm.userPhone,
        authNum: state.signupForm.authenticationNumber
      }
      store.commit('root/loadingOn')
      store.dispatch('root/requestConfirmAuthNum', param)
      .then(res => {
        store.commit('root/loadingOff')
        if (res.data.data == "true") {
          state.isDisabled = false
          state.isAuthNumConfirmed = true
          alert('.')
        } else {
          alert(' .  .')
        }
      })
      .catch(err => {
        store.commit('root/loadingOff')
        console.log(err)
      })
    }

    const clickTTS = () => {
      const text = document.querySelector('.explanation').innerText
      let source; 
      let context; 
      context = new AudioContext();
      store.dispatch('requestKakaoTTS', text)
      .then(res => {
        context.decodeAudioData(res.data, function(buffer) {
            source = context.createBufferSource();
            source.buffer = buffer;
            source.connect(context.destination);
            source.start(); 
          });  
      })
      .catch(err => {
        console.log(err)
      })
    }

    return { signupForm, state, clickSignup, clickOverlap, checkValidation, sendAuthenticationNumber, execDaumPostcode, confirmAuthenticationNumber, clickTTS }
  }
}

</script>

<style scoped>
  .el-form-item__label {
    font-size: 20px;
  }
  .signup-container {
    margin-top: 20px;
    margin-bottom: 20px;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 40px !important;
  }
  .main-content {
    height: 100%;
  }
  .right-content {
    position: relative;
    background: rgba(173, 203, 176, 0.7);
    /* height: 100%; */
    /* opacity: 0.5; */
  }
  .select-btn {
    width: 200px;
    height: 200px;
    background: #EBC86F !important;
    border-radius: 40px !important;
  }
  .select-font {
    font-family: BlackHanSans;
    font-size: 36px;
  }
  .info-font {
    text-align: center;
    font-family: SangSangFlowerRoad;
    font-size: 36px;
    color: rgba(248, 141, 141, 1);  
  }
  .question {
    font-family: SangSangFlowerRoad;
    font-size: 88px;
    color: rgba(248, 141, 141, 1);
  }
  .explanation {
    font-family: SangSangFlowerRoad;
    font-size: 48px;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
  .main-image {
    height: 442px;
    width: 700px;
    margin: 0 auto;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url('../../../assets/images/main.png');
  }
  .back-btn {
    position: absolute;
    bottom: 10px;
    left: 10px;
  }
</style>